export default {
	data() {
		return {
			isLoding: true,
			poinList: [],
			poin: '', // 积分
			current: 1, // 当前页
			total: 1, // 总共条数
			size: 5, // 每页条数
		};
	},
	onLoad(){
		this.getData()
	},
	onUnload(){
		uni.hideLoading()
	},
	methods: {
		getData(){
			let _this = this
			let userinfo = this.$store.state.userInfo
			uni.request({
				url: this.$http + '/api/goods/index',
				method: 'POST',
				data: {
					token: userinfo.token,
					size: _this.size,
					page: _this.current
				},
				success(res){
					console.log('积分商城返回数据', res)
					if(res.data.code == 1){
						_this.poin = res.data.data.user_info.score
						if(_this.poinList.length == 0){
							_this.poinList = res.data.data.data_list
						}else{
							let arr = _this.poinList
							let newArr = arr.concat(res.data.data.data_list)
							_this.poinList = newArr
						}
						
						_this.isLoding = false
					}
				}
			})
		},
		exchange(item){
			let _this = this
			let userinfo = this.$store.state.userInfo
			console.log(item)
			uni.showModal({
				title: '提示',
				content: `确定花${item.integral}积分，兑换${item.name}吗？`,
				success(res){
					if(res.confirm){
						
						uni.request({
							url: _this.$http + '/api/goods/exchange',
							method: 'POST',
							data: {
								token: userinfo.token,
								gid: item.id
							},
							success(res){
								console.log('立即兑换返回数据',res)
								if(res.data.code == 1){
									_this.poin = res.data.data
									uni.showToast({
										title: res.data.msg
									})
								}else{
									uni.showToast({
										title: res.data.msg
									})
								}
							}
						})
						
						
						
					}else{
						uni.showToast({
							title: '已取消'
						})
					}
				}
			})
		},
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
		// 返回上级菜单
		goBack(){
			uni.switchTab({
				url: '../index/index'
			})
		},
		// 跳转兑换界面
		gotocord(){
			uni.navigateTo({
				url: '../pointsrecord/pointsrecord'
			})
		},
		onReachBottom(){//触底事件
		this.current++
		this.isLoding = true
		this.getData()
		 console.log('页面到底了', this.current)
		},
	}
}
